import { makeStyles } from '@material-ui/core/styles';
import { pxToRem } from '../../../../styles/themes/vad/constants';

export default makeStyles(theme => ({
  skipToMain: {
    display: 'none',
    [theme.breakpoints.up('lg')]: {
      display: 'block',
      position: 'fixed',
      top: '-150px',
      left: '50%',
      transform: 'translateX(-50%)',
      zIndex: '1500',
      background: `${theme.mixins.White()}`,
      transition: 'top .3s ease-in',
      '&:focus': {
        top: 0,
        transition: 'top .1s ease-in',
      },
      '&:focus-within': {
        top: 0,
        transition: 'top .1s ease-in',
      },
      '& div': {
        whiteSpace: 'nowrap',
        color: 'black',
        padding: '10px 60px',
        textDecoration: 'none',
      },
    },
  },
  root: {
    flexGrow: 1,
    [theme.breakpoints.down('md')]: {
      ...theme.mixins.marginRight(0),
    },
  },
  headerContainer: {
    // paddingTop: theme.spacing(2),
    // paddingBottom: theme.spacing(2),
    minHeight: theme.spacing(10),
    [theme.breakpoints.up('lg')]: {
      minHeight: theme.spacing(16),
      paddingTop: theme.spacing(3),
      paddingBottom: theme.spacing(3),
    },
    '& .logo-red': {
      display: 'none',
    },
  },
  headerContainerPlaceholder: {
    minHeight: theme.spacing(10),
    [theme.breakpoints.up('lg')]: {
      minHeight: theme.spacing(16),
    },
  },
  headerTransparent: {
    background: 'none',
    color: theme.palette.primary.contrastText,
    transition: 'all 750ms cubic-bezier(0.94, 0.02, 0.59, 0.97)',
    '& .mega-navigation-list a:not(.active) span:not(:hover)': {
      color: theme.palette.primary.contrastText,
    },
    '&.hidden': {
      transform: 'translateY(-14rem) !important',
      opacity: 0,
    },
    '& .language-selector-wrapper': {
      '& svg path': {
        fill: theme.mixins.White(),
      },
    },
    '&.megaNavOpened': {
      '& .logo-img': {
        display: 'none',
      },
      '& .logo-red': {
        display: 'block',
      },
      '& .MuiList-root .nav-text': {
        [theme.breakpoints.up('lg')]: {
          '&::after': {
            borderColor: theme.mixins.AbuDhabiRed(),
          },
          '&:hover': {
            color: theme.mixins.AbuDhabiRed(),
          },
        },
      },
    },
    '& .MuiList-root .nav-text': {
      [theme.breakpoints.up('lg')]: {
        '&::after': {
          borderColor: theme.mixins.White(),
        },
        '&:hover': {
          color: theme.mixins.White(),
        },
      },
    },
  },
  visibleHeader: {
    zIndex: '9999',
  },
  iconDivider: {
    width: '100%',
    height: pxToRem(6),
    background: theme.mixins.White(1000),
    position: 'absolute',
    bottom: pxToRem(-5),
  },
  searchOpened: {
    [theme.breakpoints.down('md')]: {
      '&.MuiButtonBase-root': {
        borderRight: `1px solid ${theme.mixins.Black(100)}`,
        borderLeft: `1px solid ${theme.mixins.Black(100)}`,
      },
    },
  },
  // appBarHeader: {
  //   '&.page-has-notification-bar .MuiAppBar-root:not(.mui-fixed)': {
  //     position: 'absolute',
  //   },
  // },
  headerWrapper: {
    color: theme.palette.secondary.contrastText,
    background: theme.palette.secondary.main,
    boxShadow: `0px 0px 5px 0px ${theme.mixins.Black(200)}`,
    '& .mega-navigation-list a:not(.active) span:not(:hover)': {
      color: theme.palette.secondary.contrastText,
    },
    '& .MuiGrid-root': {
      background: 'transparent',
    },
    '&.hidden': {
      transform: 'translateY(-14rem) !important',
      opacity: 0,
    },
    // This is done to make header visible in case of search overlay opens
    '&.fixed-header': {
      transform: 'translateY(0) !important',
      visibility: 'visible !important',
      position: 'fixed',
    },
    minHeight: theme.spacing(10),
    [theme.breakpoints.up('lg')]: {
      minHeight: theme.spacing(16),
      boxShadow: `0px 2px 4px 0px ${theme.mixins.Black(100)}`,
      zIndex: '1305',
    },
  },
  megaNavOpened: {
    zIndex: '1310',
    position: 'fixed',
    transform: 'translateY(0) !important',
    transition: 'none !important',
    visibility: 'visible !important',
  },
  headerEditorMode: {
    position: 'absolute',
  },
  brandLogo: {
    display: 'block',
    maxWidth: theme.spacing(20),
    [theme.breakpoints.down('md')]: {
      height: theme.spacing(6),
    },
    '& img': {
      maxWidth: pxToRem(122),
      height: theme.spacing(6),
      [theme.breakpoints.up('lg')]: {
        maxWidth: '100%',
        height: theme.spacing(9),
        width: 'auto',
      },
    },
  },
  navigationListWrap: {
    ...theme.mixins.paddingLeft(theme.spacing(3)),
    ...theme.mixins.paddingRight(theme.spacing(3)),
    [theme.breakpoints.down('md')]: {
      paddingTop: 0,
      paddingBottom: 0,
      '& li:last-child': {
        borderBottom: '0',
      },
    },
  },
  linkSection: {
    display: 'flex',
    alignItems: 'center',
    height: '100%',
  },
  rightSectionWrap: {
    display: 'flex',
    alignItems: 'center',
  },
  menuOpened: {
    '&.MuiButtonBase-root': {
      ...theme.mixins.borderLeft(`1px solid ${theme.mixins.Black(100)}`),
      ...theme.mixins.marginLeft('-1px'),
    },
  },
  smallText: {
    color: 'inherit',
    display: 'block',
    paddingLeft: theme.spacing(3),
    paddingRight: theme.spacing(3),
    width: '100%',

    [theme.breakpoints.up('lg')]: {
      ...theme.mixins.paddingLR(pxToRem(30), pxToRem(30)),
      ...theme.mixins.marginRight(theme.spacing(5)),
      ...theme.mixins.borderRight(`1px solid ${theme.mixins.Black(200)}`),
      ...theme.mixins.borderLeft(`1px solid ${theme.mixins.Black(200)}`),
      paddingTop: 0,
      paddingBottom: 0,
      width: 'auto',
      position: 'relative',
    },
    '&::after': {
      [theme.breakpoints.up('lg')]: {
        display: 'block',
        content: '""',
        width: `calc(100% - ${pxToRem(60)})`,
        borderBottom: `4px solid ${theme.mixins.AbuDhabiRed()}`,
        position: 'absolute',
        transform: 'scaleX(0)',
        transition: 'transform 0.3s ease-in-out',
        top: theme.spacing(9),
      },
    },
    '&:hover': {
      color: theme.mixins.AbuDhabiRed(),
      '&::after': {
        [theme.breakpoints.up('lg')]: {
          transform: 'scaleX(1)',
        },
      },
    },
  },
  rightSection: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    [theme.breakpoints.up('xl')]: {
      justifyContent: 'flex-end',
    },
  },
  ctaBtn: {
    width: pxToRem(80),
    borderLeft: '1px solid transparent',
    borderRadius: 0,
    padding: pxToRem(28),
    cursor: 'pointer',
    ...theme.mixins.marginLeft(0),
    [theme.breakpoints.up('lg')]: {
      width: 'auto',
      padding: pxToRem(12),
    },
    '& .MuiIconButton-label': {
      [theme.breakpoints.down('md')]: {
        height: pxToRem(28),
      },
    },
  },
  menuDrawer: {
    width: '100%',
    top: pxToRem(81),
    height: `calc(100% - ${pxToRem(81)})`,
    transform: 'none !important',
    '&.disableScroll': {
      overflowY: 'hidden',
    },
  },
  searchIcon: {
    color: 'inherit',
    [theme.breakpoints.up('lg')]: {
      transition: 'background-color 0.3s ease-in-out',
      border: '2px solid',
      borderRadius: '50%',
      ...theme.mixins.marginRight(theme.spacing(3)),
    },
    '&:hover': {
      [theme.breakpoints.up('lg')]: {
        backgroundColor: theme.mixins.Black(200),
      },
    },
  },
  searchRightMargin: {
    ...theme.mixins.marginLR(0, theme.spacing(7.2)),
  },
  redSearchIcon: {
    color: theme.mixins.AbuDhabiRed(1000),
  },
  sectionDesktop: {
    display: 'none',
    [theme.breakpoints.up('lg')]: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'flex-end',
    },
  },
  sectionMobile: {
    display: 'flex',
    justifyContent: 'flex-end',
    alignItems: 'stretch',
    [theme.breakpoints.up('lg')]: {
      display: 'none',
    },
  },
  langSelectorText: {
    color: theme.mixins.Black(700),
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3),
    width: '100%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    position: 'relative',
    '& svg:last-child': {
      fontSize: theme.spacing(2),
      transform: theme.mixins.rotate0(),
    },
    '& .languageLabel': {
      display: 'flex',
      '& svg': {
        ...theme.mixins.marginRight(theme.spacing(1)),
        transform: 'rotate(0deg)',
      },
    },
  },

  flyout: {
    height: `calc(100vh - ${pxToRem(81)})`,
    left: '-100%',
    position: 'fixed',
    top: pxToRem(81),
    transition: 'all .3s ease',
    visibility: 'hidden',
    width: '100%',
    zIndex: '1002',
    overflowY: 'auto',
    backgroundColor: theme.mixins.White(),
    padding: `${theme.spacing(4)} 0`,
    '&.onScreen': {
      left: '0',
      visibility: 'visible',
    },
    '& li': {
      padding: 0,
      minHeight: 0,
      '& a': {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        width: '100%',
        padding: `${pxToRem(20)} ${theme.spacing(5)}`,
        '&.selected': {
          backgroundColor: theme.mixins.Black(30),
        },
      },
    },

    '& .backBtn': {
      marginBottom: theme.spacing(3),
      paddingLeft: theme.spacing(3),
      paddingRight: theme.spacing(3),
      textAlign: theme.mixins.textLeft(),
      '& .MuiButton-textSecondary:hover': {
        backgroundColor: 'transparent',
        color: 'inherit',
      },

      '& .MuiButton-label': {
        color: theme.mixins.Black(),
        flexDirection: theme.mixins.flexDirection(),
      },
      '& .MuiButton-startIcon': {
        transform: theme.mixins.rotate180(),
        padding: '0',
        width: 'auto',
      },
      '& > span:hover': {
        '& .MuiButton-label': {
          color: 'inherit',
        },
        '& .MuiButton-startIcon': {
          color: 'inherit',
          backgroundColor: 'transparent',
        },
      },
    },
  },
  '@global': {
    '.no-flip, a[href^="tel:"]': {
      unicodeBidi: 'bidi-override',
      direction: 'ltr',
      textAlign: theme.mixins.textLeft(),
    },
  },
}));
